@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{
    padding:0;
    margin: 0;
    box-sizing:border-box;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 0.05rem;
    font-weight:400;
}

img{
    max-width: 500px;
    max-height: 400px;
}

body{
    width: 100vw;
    overflow-x:hidden;
    max-width: 100%;
    background-color: #F5F5F5;
}

a{
    text-decoration: none;
}


/*ShopScreen*/

.page{
    width:100vw;
    display:flex;
    flex-direction:column;
    align-items: center;
}

/*****FOOTER*****/

footer{
    width: 100%;
    background-color:#20B2AA;
    text-align:center;
    align-self: flex-end;
}

/*****HEADER*****/

header{
    height: 3rem;
    background-color:#20B2AA;
    width:100%;
    display:flex;
    align-items:center;
    text-transform: uppercase;
}

nav{
    overflow:hidden;
    height:0;
}

.link{
    padding: 0.5rem;
    text-align:center;
    text-decoration:none;
    font-weight: 700;
    color: white;
    transition: background-color 0.3s ease;
}

.link:hover{
    background-color:#008B8B;
}


.toggleButton{
   position:absolute;
   right:0px;
}

.menu-drop{
    top: 3rem;
    display:flex;
    flex-direction:column;
    align-items:center;
    position:absolute;
    background-color:#20B2AA;
    width:100%;
    height:9rem;
    z-index:999;
    transition: height 0.3s ease;
}

.menu-drop .link{
    width:100%;
}

.user-data{
    display:flex;
    justify-content: center;
    align-items:center;
    width: 100%;
}

.user-data p{
    margin-right: 0.5rem;
}

.logout{
    cursor: pointer;
    border:1px solid rgba(0, 0, 0, 0.507);
    border-radius: 10%;
    background-color: white;
    padding: 0.3rem;
    transition: background-color 0.3s ease;
    display:flex;
    justify-content: center;
}


.logout:hover{
    background-color: #B22222;
}

@media screen and (min-width:800px) {

    .logout{
        cursor:pointer;
        margin-right: 1rem;
    }
    
    .user-data{
        margin-left: 1rem;
        display:flex;
        position: absolute;
        right: 0;
        align-items:center;
        width: auto;
    }

    nav{
        height: inherit;
        overflow:visible;
        display:flex;
        align-items: center;
    }

    .link{
        height: inherit;
        display:flex;
        align-items:center;
    }

    .toggleButton{
        display:none;
    }

    header{
        height: 3rem;
    }

}

.icon{
    font-size: 25px;
    margin-right: 1rem;
}

.icon:hover{
    cursor:pointer;
}


/*loader*/

.box-loader{
    display:flex;
    width: 100vw;
    height: 100vh;
    justify-content:center;
    align-items:center;
}

.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
  }
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

/*buttons*/
button{
    padding: 5px;
    margin: 5px;
    cursor: pointer;
    background-color: #20B2AA;
    border: 1px;
    border-radius: 3px;
    transition: background-color 1s ease;
    color: white;
}

button:hover{
    background-color: #C0C0C0;
}

.btn-delete{
    background-color: #B22222;
}

.btn-edit{
    background-color: #2E8B57;
}

.btn-green:hover{
    cursor:pointer;
}

.btn-green{
    width:100%;
    background-color:rgba(37, 207, 22, 0.712);
}

.btnDisabled{
    background-color:#C0C0C0;
    cursor:not-allowed !important; 
}

/*tables styles*/
table{
    border-collapse: collapse;
    margin: 2rem;
}

th, tr, td{
    border: 1px solid rgba(0, 0, 0, 0.582);
}

tr{
    cursor:default;
}

tr:hover{
    background-color:#F0F8FF;
}

th{
    padding: 0.5rem;
    text-transform: uppercase;
    background-color: #dae2e9;
}

td{
    padding:0.5rem;
}

.navlink{
    color: #20B2AA;
    font-weight: bold;
}
/*LIST PRODUCTS*/

.containerItems{
    margin: 1rem;
    width:100%;
    display:grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1024px;
}

@media screen and (max-width: 650px){
    .containerItems{
        grid-template-columns: 1fr !important;
    }

}

.itemCard{
    position:relative;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.425);
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content: space-between;
    background-color: #F0F8FF;
}

.itemCard:hover{
    cursor:pointer;
    background-color:#dae2e9;
}

.imgProduct{
    width: 75%;
    height: 80%;
}

.titleProduct{
    color: black;
    font-weight: 400;
    text-transform: uppercase;
    font-size: 20px;
}


.bottomProduct{
    margin-top:5px;
    display:flex;
    flex-direction: column;
    align-items:center;
}


/* modal */

.modalBox{
    display:flex;
    flex-direction: column;
    align-items:center;
    position: fixed;
    top: 5%;
    left: 10%;
    width: 80vw;
    height: 75vh;
    border: 0.1rem solid black;
    background-color:white;
    z-index:999;
    padding: 0.5rem;
}

.imgProductModal{
    width: 280px;
    height: 320px;
}

.descProduct{
    text-align: center;
    color:rgba(0, 0, 0, 0.479);
}

.btnCloseModal{
    position: absolute;
    top: 0;
    right: 0;
}

.filterBox{

    position: relative;
    border: 0.1rem solid black;
    display:flex;
    justify-content:space-around;
    width:100vw;
}



@media screen and (max-width:600px){
    th, tr, td{
        font-size: 12px;
    }
    .orderId{
        font-size: 8px;
    }
    th{
        padding:0;
    }
    table{
        margin: 1rem;
    }
}

.btnOrder{

    border-radius: 0;
}

.disabled{
    cursor: not-allowed;
    pointer-events: none;
    background-color: grey;
}

.cart-order-form{
    margin-left: 2rem;
    margin-right: 2rem;
    display:flex;
    flex-direction: column;
    align-items:center;
}

.addComment {
    resize: none;
}

.btnPag{
    cursor:pointer;
}

.openModalBackGround{
    position: relative;
    background-color:rgba(0, 0, 0, 0.575);
    width:100%;
    height:100%;
}

.screen-table{
    height: 90vh;
    display:flex;
    align-items:center;
    justify-content:flex-start;
    flex-direction: column;
}

.screen{
    height:100vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

body{
    margin:0;
    box-sizing: border-box;
}

.loginBox{
    width: 100vw;
    height:100vh;
    display:flex;
    justify-items: center;
    text-align:center;
}

form{
    margin:auto;
    background-color: #e0e4e4;
    border-radius: 20px;
    border: 1px solid #17191b7a;
    height: 500px;
    padding: 20px;
    width: 320px;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content:space-around;
}

.boxInput{
    position: relative;
    width: 100%;
}

.inputForm{
    border: 1px solid #778899c5;
    border-radius: 3px;
    color: rgb(0, 0, 0);
    font-size: 18px;
    outline: 0;
    padding: 1rem;
    width: 100%;
    font-size: 14px;
}

.placeholder{
    font-size: 12px;
    position: absolute;
    z-index: 999;
    top: 30%;
    left: 6%;
    color:rgb(146, 149, 150);
}

.inputForm:focus + .placeholder{
    font-size: 12px;
    -webkit-transform: translate(0,-78%);
            transform: translate(0,-78%);
}

.placeholderWithText{
    font-size: 12px;
    -webkit-transform: translate(0,-78%);
            transform: translate(0,-78%);
}

input[type="submit"]{
    padding: 5px;
    width: 100%;
    border: 1px;
    background-color: #20B2AA;
    border-radius: 3px;
    cursor:pointer;
    transition: background-color 1s ease;
    color: white;
}
input[type="submit"]:hover{
    background-color: #b1aeae;
}





.msgAuth{
    text-transform: uppercase;
    font-weight: 500;
}

